import React, { Component } from 'react';
import { ListBlock } from './../../containers/containers';
import { PaginationBlock } from './../../containers/containers';
import NoIssues from './../noIssues/NoIssues';
import Loading from './../loading/Loading';
import PropTypes from 'prop-types';
import { validatePageNumber, validateItemsPerPage } from './../../helpers';
import { parse } from 'qs';
import './Result.css';

class Result extends Component {

	componentWillMount() {

		const { name, repo} = this.props.match.params;

		let { page, per_page } = parse(this.props.location.search.substr(1));

		page = validatePageNumber(page);
		per_page = validateItemsPerPage(per_page);

		this.props.getIssues(name, repo, page, per_page);
	}

	componentWillReceiveProps(nextProps) {
   
    if (this.props.location.pathname !== nextProps.location.pathname || 
    	this.props.location.search !== nextProps.location.search
    	) {

    	const { name, repo} = nextProps.match.params;

    	let { page, per_page } = parse(nextProps.location.search.substr(1));

    	page = validatePageNumber(page);
			per_page = validateItemsPerPage(per_page);

      this.props.getIssues(name, repo, page, per_page);
    }
  }

	render() {

    const { isLoading, issuesLength, location } = this.props;

		let { page } = parse(location.search.substr(1));

		page = validatePageNumber(page);

		if (isLoading) {

			return <Loading />
			
		} else if (issuesLength === 0) {
			
			return <NoIssues />

		} else {

			return (
				<div className='Result'>

					<ListBlock />

					<PaginationBlock
						currentPage={page}
					/>

				</div>
			)

		}
	}
	
}

Result.defaultProps = {
  issuesLength: 0,
  isLoading: false,
  getIssues: () => {}
}

Result.propTypes = {
  location: PropTypes.object.isRequired,
	issuesLength: PropTypes.number.isRequired,
  isLoading: PropTypes.bool.isRequired,
  getIssues: PropTypes.func.isRequired
}

export default Result


